* {
  margin: 0;
  padding: 0;
}

html, body {
  /* fondo con degradado */
  background: linear-gradient(-45deg, #300050, #3d0063, #6b01b2, #148fc8);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100vh;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.App {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.areaJuego {
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items:flex-start;
  justify-content:space-between;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-align: center;
}

/* Estilos compartidos para áreas de juego del usuario y la máquina */
.areaUsuario, .areaMaquina {
  width: 35%;
  margin-top: 1.5vw;
}

/* Estilos para el contenedor de datos o parte central */
.areaDatos {
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
}